<template>
  <div class="container py-5">
    <h3>{{ env }}</h3>
    <hr />

    <div class="row" id="dashboard-cards">
      <div class="col-12 mb-5">
        <div class="card">
          <div class="card-header" data-toggle="collapse" data-target="#Ongoing-collapse">
            <h5 class="mb-0">
              Ongoing Bids
              <span class="badge badge-primary float-right">1</span>
            </h5>
          </div>
          <div class="card-body collapse" id="Ongoing-collapse" data-parent="#dashboard-cards">
            <ul class="list-group">
              <table class="table table-bordered">
                <thead>
                  <tr>
                    <th>Name</th>
                    <th>Status</th>
                    <th>Signed</th>
                    <th>Due</th>
                    <th>Extended</th>
                    <th>Duration</th>
                    <th>Remaining</th>
                    <th>LD</th>
                  </tr>
                </thead>
                <tbody v-if="statuses">
                  <tr v-for="(bid, index) in ongoingBids" v-bind:key="index">
                    <td>{{ bid.name }}</td>
                    <!-- <td>
                      <a
                        href
                        data-toggle="modal"
                        data-target="#changeStatusModal"
                        @click="setModalBid(bid)"
                        :class="'badge badge-'+bidStatusColor(bid)"
                      >{{ bid.status_id }}</a>
                    </td> -->
                    <td>
                      <div class="d-flex flex-row align-items-center">
                        <b-dropdown
                          size="sm"
                          :variant="bidStatusColor(bid)"
                          toggle-class="text-decoration-none"
                          no-caret
                        >
                          <template v-slot:button-content>
                            <span>{{ bid.status_id }}</span>
                            <span class="sr-only">Edit status</span>
                          </template>

                          <b-dropdown-item
                            href="#"
                            v-for="(color, status) in statuses"
                            :key="status"
                            @click="chageStatus2(bid, status)"
                          >{{ status }}</b-dropdown-item>
                        </b-dropdown>
                      </div>
                    </td>
                    <td>{{ bid.agreement_date }}</td>
                    <td>{{ bid.due_date }}</td>
                    <td>{{ bid.extended_date }}</td>
                    <td>{{ bid.duration }}</td>
                    <td>{{ bid.remaining }}</td>
                    <td>{{ bid.cost }}</td>
                  </tr>
                </tbody>
              </table>
            </ul>
          </div>
        </div>
      </div>
    </div>

    <div class="modal" tabindex="-1" role="dialog" id="changeStatusModal">
      <div class="modal-dialog modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Change Status</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>

          <div class="modal-body">
            <div class="row">
              <div class="form-group col">
                <select
                  v-if="modalBid"
                  class="form-control"
                  name="status_id"
                  :value="modalBid.status_id"
                  v-model="selected"
                >
                  <option
                    v-for="(color, status) in statuses"
                    :key="status"
                    :value="status"
                  >{{ status }}</option>
                </select>
              </div>
            </div>
          </div>

          <div class="modal-footer">
            <button @click="changeStatus" type="button" class="btn btn-primary">Save changes</button>
            <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Dashboard",

  components: {},

  data() {
    return {
      ongoingBids: null,
      statuses: null,
      modalBid: null,
      selected: null,
      env: process.env,
    };
  },

  mounted() {
    this.getOngoingBids();
    this.getStatuses();
  },

  methods: {
    getOngoingBids: function() {
      axios.get("http://localhost:8000/api/ongoingBids").then(response => {
        this.ongoingBids = response.data;
      });
    },

    getStatuses: function() {
      axios.get("http://localhost:8000/api/statuses").then(response => {
        this.statuses = response.data;
      });
    },

    bidStatusColor: function(bid) {
      return this.statuses[bid.status_id];
    },

    setModalBid: function(bid) {
      this.modalBid = bid;
      this.selected = this.modalBid.status_id;
    },

    chageStatus2(bid, selectedStatus) {
      this.modalBid = bid;
      this.selected = selectedStatus;
      this.changeStatus();
    },

    changeStatus: function() {
      axios
        .post("http://localhost:8000/api/bids/" + this.modalBid.id, {
          status_id: this.selected
        })
        .then(response => (this.responseData = response.data))
        .then(() => {
          this.getOngoingBids();
          document.getElementById("changeStatusModal").click();
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>